@import "https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap";
*, :before, :after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  width: 100%;
  overflow-x: hidden;
}

:root {
  --bgColor: white;
  --color: #222;
}

@media (prefers-color-scheme: dark) {
  :root {
    --bgColor: #222;
    --color: white;
  }
}

.container {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 15px;
  display: block;
}

body {
  color: var(--color);
  background-color: var(--bgColor);
  grid-template-columns: auto auto;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  font-family: Ubuntu, sans-serif;
  display: grid;
}

.canvas {
  border: 1px solid var(--color);
  width: 48vw;
  height: 80vh;
  display: block;
}

@property --colorRing1 {
  syntax: "<color>";
  inherits: false;
  initial-value: #fff;
}

@property --colorRing2 {
  syntax: "<color>";
  inherits: false;
  initial-value: green;
}

@property --percentRing1 {
  syntax: "<number>";
  inherits: false;
  initial-value: 20;
}

@property --percentRing2 {
  syntax: "<number>";
  inherits: false;
  initial-value: 80;
}

.paint {
  --colorRing1: red;
  --colorRing2: yellow;
  --percentRing1: 20;
  --percentRing2: 80;
  border: 1px solid var(--color);
  background-image: paint(ring);
  width: 48vw;
  height: 80vh;
  animation: 10s ease-in-out 1s infinite alternate animRing;
}

@keyframes animRing {
  0% {
    --percentRing1: 20;
    --percentRing2: 80;
    --colorRing1: red;
    --colorRing2: yellow;
  }

  20% {
    --percentRing1: 80;
    --percentRing2: 20;
    --colorRing1: blue;
    --colorRing2: green;
  }

  40% {
    --percentRing1: 50;
    --percentRing2: 50;
    --colorRing1: purple;
    --colorRing2: orange;
  }

  60% {
    --percentRing1: 70;
    --percentRing2: 30;
    --colorRing1: yellow;
    --colorRing2: pink;
  }

  80% {
    --percentRing1: 40;
    --percentRing2: 60;
    --colorRing1: green;
    --colorRing2: purple;
  }

  100% {
    --percentRing1: 90;
    --percentRing2: 10;
    --colorRing1: orange;
    --colorRing2: red;
  }
}

._hidden {
  display: none;
}
